@mixin basicTextStyle($weight, $size, $line-height) {
  margin: 0;
  font-weight: $weight;
  font-size: $size;
  line-height: $line-height;
}

@mixin commonBorder($color) {
  border: $border-thickness solid $color;
}

@mixin hover {
  transition: all $transition-duration;
  &:hover {
    @content;
  }
}
